/**
 * Êó•ÊúüÊ†ºÂºèËΩ¨Êç¢Â∑•ÂÖ∑
 *
 * ÈóÆÈ¢òÔºöiOS ‰∏çÊîØÊåÅ "2025-10-22 11:35:45" Ê†ºÂºè
 * Ëß£ÂÜ≥ÔºöËΩ¨Êç¢‰∏∫ ISO 8601 Ê†ºÂºè "2025-10-22T11:35:45"
 *
 * ÊîØÊåÅÁöÑÊ†ºÂºèÔºö
 * - "yyyy/MM/dd"
 * - "yyyy/MM/dd HH:mm:ss"
 * - "yyyy-MM-dd"
 * - "yyyy-MM-ddTHH:mm:ss"
 * - "yyyy-MM-ddTHH:mm:ss+HH:mm"
 */

/**
 * Â∞ÜÊó•ÊúüÂ≠óÁ¨¶‰∏≤ËΩ¨Êç¢‰∏∫ iOS ÂÖºÂÆπÁöÑÊ†ºÂºè
 * @param dateStr Êó•ÊúüÂ≠óÁ¨¶‰∏≤ÔºåÂ¶Ç "2025-10-22 11:35:45"
 * @returns iOS ÂÖºÂÆπÁöÑÊó•ÊúüÂ≠óÁ¨¶‰∏≤ÔºåÂ¶Ç "2025-10-22T11:35:45"
 */
export const toIOSCompatibleDate = (dateStr: string | undefined | null): string | null => {
  if (!dateStr) {
    return null
  }

  // Â∑≤ÁªèÊòØ ISO 8601 Ê†ºÂºèÔºåÁõ¥Êé•ËøîÂõû
  if (dateStr.includes('T')) {
    return dateStr
  }

  // Â∞ÜÁ©∫Ê†ºÊõøÊç¢‰∏∫ TÔºåËΩ¨Êç¢‰∏∫ ISO 8601 Ê†ºÂºè
  // "2025-10-22 11:35:45" ‚Üí "2025-10-22T11:35:45"
  return dateStr.replace(' ', 'T')
}

/**
 * ÂÆâÂÖ®Âú∞Ëß£ÊûêÊó•ÊúüÂ≠óÁ¨¶‰∏≤
 * @param dateStr Êó•ÊúüÂ≠óÁ¨¶‰∏≤
 * @returns Date ÂØπË±°ÔºåÂ¶ÇÊûúËß£ÊûêÂ§±Ë¥•ËøîÂõû null
 */
export const parseDate = (dateStr: string | undefined | null): Date | null => {
  if (!dateStr) {
    return null
  }

  // ËΩ¨Êç¢‰∏∫ iOS ÂÖºÂÆπÊ†ºÂºèÂÜçËß£Êûê
  const isoDate = toIOSCompatibleDate(dateStr)
  if (!isoDate) {
    return null
  }

  const date = new Date(isoDate)

  // Ê£ÄÊü•ÊòØÂê¶‰∏∫ÊúâÊïàÁöÑÊó•Êúü
  if (isNaN(date.getTime())) {
    console.warn('‚ö†Ô∏è Êó†ÊïàÁöÑÊó•ÊúüÊ†ºÂºè:', dateStr)
    return null
  }

  return date
}

/**
 * Ê†ºÂºèÂåñÊó•Êúü‰∏∫Êú¨Âú∞Ê†ºÂºè
 * @param dateStr Êó•ÊúüÂ≠óÁ¨¶‰∏≤
 * @param format Ê†ºÂºèÂ≠óÁ¨¶‰∏≤ÔºåÂ¶Ç "yyyy-MM-dd HH:mm"
 * @returns Ê†ºÂºèÂåñÂêéÁöÑÊó•ÊúüÂ≠óÁ¨¶‰∏≤
 */
export const formatDate = (
  dateStr: string | undefined | null,
  format: string = 'yyyy-MM-dd HH:mm'
): string => {
  const date = parseDate(dateStr)
  if (!date) {
    return ''
  }

  const year = date.getFullYear()
  const month = String(date.getMonth() + 1).padStart(2, '0')
  const day = String(date.getDate()).padStart(2, '0')
  const hours = String(date.getHours()).padStart(2, '0')
  const minutes = String(date.getMinutes()).padStart(2, '0')
  const seconds = String(date.getSeconds()).padStart(2, '0')

  return format
    .replace('yyyy', String(year))
    .replace('MM', month)
    .replace('dd', day)
    .replace('HH', hours)
    .replace('mm', minutes)
    .replace('ss', seconds)
}
